@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.container {
  display: flex;
  flex-direction: column;
  background-color: rgb(24, 27, 48);
  color: white;
  padding: 3rem;
  font-family: 'Roboto', sans-serif;;
}

select {
  width: 300px;
  height: 30px;
}

.test-types {
  display: flex;
  justify-content: space-evenly;
}

.question-container {
  margin-top: 20px;
  padding-left: 25rem;
  padding-right: 25rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.question-box {
  margin-top: 30px;
}

.retry-btn {
  margin-top: 2rem;
  width: 30%;
  height: 30px;
  align-self: center;
  cursor: pointer;
  background-color: rgb(74, 36, 240);
  border: 0;
  color: #fff;
}

input[type="submit"] {
  background-color: rgb(74, 36, 240);
  border: 0;
  width: 90%;
  height: 3rem;
  margin: 2rem auto;
  cursor: pointer;
  color: inherit;
  font-size: 2rem;
  letter-spacing: 2px;
  font-weight: bold;
}


.containter-input {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.containter-input input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}

.containter-input:hover input ~ .checkmark {
  background-color: #ccc;
}

.containter-input input:checked ~ .checkmark {
  background-color: #2196f3;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.containter-input input:checked ~ .checkmark:after {
  display: block;
}
.containter-input .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}

.grade-heading {
  text-align: center;
}
